import { IntroSizing, PatternPartGroup } from "../../utils/types";
import {
  Size,
  baseBodySizing,
  vestCoreParts,
  VestMeasurement,
  FOMeasurement,
  CustomizedFO,
} from "../../utils/sizeBase";
import profile from "./profile";
import { GaugeProps, getPattern, getTotalSts } from "../../patternMakers/vest";
import { getRound, Chart, getBasePatternFromChart } from "@tanyoknits/shared";
import { Needle } from "../../utils/needles";
import { getFOMeasurementBySize, getIntroSizingInfo } from "../../utils/misc";

// Size list
export const sizes: Size[] = [
  "2XS",
  "XS",
  "S",
  "M",
  "L",
  "XL",
  "2XL",
  "3XL",
  "4XL",
];

const customizedSize: CustomizedFO = {
  bust_circumference: [30, 32.5, 35, 39, 43, 47, 52, 56, 60],
  total_length: [22, 23, 24, 25, 26, 27, null],
  neck_depth: [3.75, 4, 4.25, 4.5, 4.75, null],
  neck_band_height: [1.5, null, null, null, null, 2, null],
  arm_band_height: [1.5, null, null, null, null, 2, null],
  hem: [2.5, null],
};

export const measurements: FOMeasurement[] = getFOMeasurementBySize(
  sizes,
  vestCoreParts,
  customizedSize
);

export function getGaugeItems(
  sizeID: number,
  halfBody: number,
  stsGauge: number,
  rowGauge: number
): GaugeProps {
  const ms = measurements[sizeID] as VestMeasurement;

  // width
  const neck = getRound(ms.neck_width * stsGauge);
  const shoulder = getRound(ms.shoulder_width * stsGauge);

  // Center pattern is an odd number, so neck width should be odd sts too.
  const adjNeck = getRound(neck, 2) - 1;
  const adjArmInset = (halfBody - adjNeck - shoulder * 2) / 2;
  const side = Math.floor((halfBody - adjNeck) / 2);

  // depth
  const neckBack = getRound(ms.neck_back_depth * rowGauge, 2);
  const neckFront = getRound(ms.neck_depth * rowGauge, 2);
  const shoulderDrop = getRound(ms.shoulder_drop * rowGauge, 2);
  const shoulderToArm = getRound(ms.armhole_depth * rowGauge, 2);
  const armCurve = getRound(ms.underarm_curve_depth * rowGauge, 2);
  const shoulderToArmInset = shoulderToArm - armCurve;

  // band
  const neckBand = getRound(ms.neck_band_height * rowGauge);
  const armBand = getRound(ms.arm_band_height * rowGauge);
  const hem = getRound(ms.hem * rowGauge);

  const totalRowCount = getRound(ms.total_length * rowGauge);

  const adjustedSize: GaugeProps = {
    // Since motifs have their own St equiv stitch count, adjust half body sts
    halfBody,
    neck: adjNeck,
    armInset: adjArmInset,
    shoulder,
    side,
    neckBack,
    neckFront,
    shoulderDrop,
    shoulderToArm,
    armCurve,
    shoulderToArmInset,
    neckBand,
    armBand,
    hem,
    totalRowCount,
  };

  return adjustedSize;
}

export function getPatternByPartGroup(
  backChart: Chart,
  frontChart: Chart,
  stsGauge: number,
  rowGauge: number,
  gaugeItems: GaugeProps,
  coNeedle: Needle,
  ribNeedle: Needle,
  motifOffset: number = 0
): PatternPartGroup[] {
  // make a base pattern from motif repeats

  const backBasePattern = getBasePatternFromChart(
    backChart,
    gaugeItems.totalRowCount,
    motifOffset
  );
  const frontBasePattern = getBasePatternFromChart(
    frontChart,
    gaugeItems.totalRowCount,
    motifOffset
  );

  const chartByPart = getPattern(
    backBasePattern,
    frontBasePattern,
    stsGauge,
    rowGauge,
    gaugeItems,
    coNeedle,
    ribNeedle
  );

  // const frontChartByPart = getFrontPattern(frontBasePattern, gaugeItems, coNeedle);
  return chartByPart;
}

const totalStsBySize: number[] = measurements.map((m, i) => {
  const schema = getTotalSts(
    m as VestMeasurement,
    profile.gauge.sts_count,
    profile.gauge.row_count
  );

  return schema;
});

export const introSizing: IntroSizing = getIntroSizingInfo(
  sizes,
  baseBodySizing,
  measurements,
  totalStsBySize,
  ["bust"],
  ["bust_circumference", "total_length"],
  { size: "S", grams: 260 }
);
