import {
  getChartFromText,
  getRightLeftFlippedChart,
} from "../../utils/chartModifier";
import { Motif } from "../../utils/types";
import { getRound } from "../../utils";

export const getMotif = (
  stsCount: 8 | 6 | 4 | 2 = 2,
  rowCount: 12 | 8 | 6 | 4 | 2 = 2,
  direction: "right" | "left" = "right"
): Motif => {
  let cable = null;
  switch (stsCount) {
    case 8:
      cable = direction === "right" ? "c8r" : "c8l";
      break;
    case 6:
      cable = direction === "right" ? "c6r" : "c6l";
      break;
    case 4:
      cable = direction === "right" ? "c4r" : "c4l";
      break;
    case 2:
      cable = direction === "right" ? "c2r" : "c2l";
      break;
    default:
      cable = direction === "right" ? "c2r" : "c2l";
      break;
  }

  return {
    title: "Cable",
    chart: getChartFromText([cable, `k:${stsCount} * ${rowCount - 1}`]),
    stsCount,
    stEquivCount: getRound(((stsCount * 9) / 10) * 1000, 125) / 1000,
  };
};
