import { ChartRow, Direction } from "../utils/types";
import { FocusType } from "./Chart";
import { WrittenDisplayProps } from "./Written";
import { css, cx } from "@linaria/core";
import { unit } from "./styles";
import { patternStyles, motifColors } from "./styles";
import { isWSRow } from "../utils";
import { getHumanReadableSts } from "../utils/stsRenderer";

interface Props {
  row: ChartRow;
  rowID: number;
  direction: Direction;
  display: WrittenDisplayProps;
  focus?: FocusType | undefined | null;
  className?: string;
}

const styles = {
  rowRS: css`
    display: flex;
    flex-wrap: wrap;
    gap: ${unit / 2}px;
    div {
      white-space: nowrap;
      &:not(:last-child) {
        &:after {
          content: ", ";
        }
      }
    }
  `,
  rowRSBlock: css`
    display: flex;
    flex-direction: column;
    margin-top: ${unit}px;
    padding-left: ${unit}px;
    padding-right: ${unit}px;
    padding-bottom: ${unit}px;

    div {
      width: fit-content;
      white-space: nowrap;
    }
  `,
  rowWS: css`
    display: flex;
    flex-direction: row-reverse;
    justify-content: flex-end;
    flex-wrap: wrap;
    gap: ${unit / 2}px;
    div {
      &:not(:first-child) {
        &:after {
          content: ", ";
        }
      }
    }
  `,
  rowWSBlock: css`
    display: flex;
    flex-direction: column-reverse;
    margin-top: ${unit}px;
    padding-left: ${unit}px;
    padding-right: ${unit}px;
    padding-bottom: ${unit}px;
    div {
      width: fit-content;
      white-space: nowrap;
    }
  `,
  textSts: css`
    padding-left: ${unit / 4}px;
    padding-right: ${unit / 4}px;
  `,
};

export default function WrittenRow({
  row,
  rowID,
  direction,
  display,
  focus,
  className,
}: Props) {
  return (
    <div
      className={cx(
        isWSRow(direction, rowID)
          ? display === "inline"
            ? styles.rowWS
            : styles.rowWSBlock
          : display === "inline"
          ? styles.rowRS
          : styles.rowRSBlock,
        className
      )}
    >
      {row.map((stsGroup, j) => {
        const stsGroupText = getHumanReadableSts(
          stsGroup.sts,
          isWSRow(direction, rowID)
        );
        const { motif } = stsGroup;
        return (
          stsGroupText.length > 0 && (
            <div
              key={j}
              className={cx(
                styles.textSts,
                rowID === focus?.[0] &&
                  j === focus?.[1] &&
                  patternStyles.selectedSts,
                motif?.id != null && motifColors[motif?.id]
              )}
            >
              {stsGroupText.join(", ")}
            </div>
          )
        );
      })}
    </div>
  );
}
